import {
    Button,
    Container,
    Image, 
    Dimmer,
    Segment
} from 'semantic-ui-react'; 
import React from 'react'


 class PublicDomainReader extends React.Component {
    render(){
        return(
            <div>
                <Dimmer active={true} onClickOutside={this.handleHide}>
                    <Container>
                        <Image src='/static/pdr-gif-1.gif' size="massive" verticalAlign="top"/>
                    </Container>
                </Dimmer>
            </div>
        )
    }
}

export default PublicDomainReader


